footer .fa{
    animation: heartbeat 1s infinite;
    transition: all .2s ease-in-out;
}
  
@keyframes heartbeat
{
    0%
    {
      transform: scale( .75 );
    }
    20%
    {
      transform: scale( 1 );
      color: red;
    }
    40%
    {
      transform: scale( .75 );
    }
    60%
    {
      transform: scale( 1 );
      color: red;
    }
    80%
    {
      transform: scale( .75 );
    }
    100%
    {
      transform: scale( .75 );
    }
}
  
