body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer .fa{
    -webkit-animation: heartbeat 1s infinite;
            animation: heartbeat 1s infinite;
    transition: all .2s ease-in-out;
}
  
@-webkit-keyframes heartbeat
{
    0%
    {
      transform: scale( .75 );
    }
    20%
    {
      transform: scale( 1 );
      color: red;
    }
    40%
    {
      transform: scale( .75 );
    }
    60%
    {
      transform: scale( 1 );
      color: red;
    }
    80%
    {
      transform: scale( .75 );
    }
    100%
    {
      transform: scale( .75 );
    }
}
  
@keyframes heartbeat
{
    0%
    {
      transform: scale( .75 );
    }
    20%
    {
      transform: scale( 1 );
      color: red;
    }
    40%
    {
      transform: scale( .75 );
    }
    60%
    {
      transform: scale( 1 );
      color: red;
    }
    80%
    {
      transform: scale( .75 );
    }
    100%
    {
      transform: scale( .75 );
    }
}
  
.alert-danger.cust-error {
    background: transparent;
    border-color: #1f1e1c;
    border-radius: 0;
    padding: 3px 12px;
    font-size: 13px;
}
.lh-28px {
  line-height: 28px !important;
}
.cursor-p {
  cursor: pointer !important;
}

.custom-social-icons li.social-icons-instagram a {
  /* background: radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%), radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%), radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%), radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%), radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%), radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%), radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent), linear-gradient(#6559ca, #bc318f 30%, #e33f5f 50%, #f77638 70%, #fec66d 100%); */
  background: #E1306C;
}
.social-icons li:hover.social-icons-instagram a {
  background: #cc275e !important;
}
.social-icons li a {
  font-size: 14px !important;
}
.custom-feature-box .feature-box-info.custom-right-side-border {
  border-right: 1px solid #52504d;
  /* border-left: 1px solid #52504d; */
  line-height: 1;
  padding: 0px 16px 0 0;
  margin: 0 13px 0 1px;
}



