	min-height: 7.2vw;
}

.curved-border:after {
	content: '';
	background-image: url(/static/media/curved-border.eec002c2.svg);
	background-size: 100% 100%;
	background-position: center;
	position: absolute;
	left: 0;
	bottom: -2px;
	width: 100vw;
	height: 7.2vw;
	z-index: 20;
}

.curved-border.curved-border-top:after {
