/* Toggles */
.toggle {
	margin: 10px 0 20px;
	position: relative;
	clear: both;
}

.toggle .toggle {
	margin: 10px 0 0;
}

.toggle > input {
	cursor: pointer;
	filter: alpha(opacity=0);
	height: 45px;
	margin: 0;
	opacity: 0;
	position: absolute;
	width: 100%;
	z-index: 2;
}

.toggle > label {
	transition: all .15s ease-out;
	background: #f7f7f7;
	border-left: 3px solid #CCC;
	border-radius: 5px;
	color: #CCC;
	display: block;
	min-height: 20px;
	padding: 12px 20px 12px 10px;
	position: relative;
	cursor: pointer;
	font-weight: 400;
}

.toggle > label:-moz-selection {
	background: none;
}

.toggle > label i.fa-minus {
	display: none;
}

.toggle > label i.fa-plus {
	display: inline;
}

.toggle > label:before {
	content: " ";
	position: absolute;
	top: calc(50% + 1px);
	right: 14px;
	border-color: #CCC;
	border-top: 1px solid;
	border-right: 1px solid;
	width: 8px;
	height: 8px;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
	transition: transform 0.3s, top 0.3s;
	transform: rotate(-45deg) translate3d(0, -50%, 0);
	transform-origin: 35%;
}

.toggle > label:hover {
	background: #f5f5f5;
}

.toggle > label + p {
	color: #999;
	display: block;
	overflow: hidden;
	padding-left: 30px;
	text-overflow: ellipsis;
	white-space: nowrap;
	height: 25px;
}

.toggle > label i {
	font-size: 0.7em;
	margin-right: 8px;
	position: relative;
	top: -1px;
}

.toggle > .toggle-content {
	display: none;
}

.toggle > .toggle-content > p {
	margin-bottom: 0;
	padding: 10px 0;
}

.toggle.active i.fa-minus {
	display: inline;
}

.toggle.active i.fa-plus {
	display: none;
}

.toggle.active > label {
	background: #f7f7f7;
	border-color: #CCC;
}

.toggle.active > label:before {
	top: calc(50% - 8px);
	transform: rotate(135deg);
}

.toggle.active > p {
	white-space: normal;
}

.toggle > p.preview-active {
	height: auto;
	white-space: normal;
}

.toggle.toggle-sm .toggle > label {
	font-size: 0.9em;
	padding: 6px 20px 6px 10px;
}

.toggle.toggle-sm .toggle > .toggle-content > p {
	font-size: 0.9em;
}

.toggle.toggle-lg .toggle > label {
	font-size: 1.1em;
	padding: 16px 20px 16px 20px;
}

.toggle.toggle-lg .toggle > .toggle-content > p {
	font-size: 1.1em;
}

.toggle.toggle-simple .toggle > label {
	border-radius: 0;
	border: 0;
	background: transparent;
	color: #777;
	padding-left: 32px;
	margin-bottom: -10px;
	font-size: 1em;
	font-weight: 600;
	color: #212529;
}

.toggle.toggle-simple .toggle > label:after {
	content: '';
	position: absolute;
	left: 0;
	display: block;
	width: 24px;
	height: 24px;
	background: #CCC;
	top: 25px;
	margin-top: -12px;
	border-radius: 3px;
	z-index: 1;
}

.toggle.toggle-simple .toggle > label:before {
	border: 0;
	left: 9px;
	right: auto;
	margin-top: 0;
	top: 12px;
	font-family: 'Font Awesome 5 Free';
	font-weight: 900;
	content: "\f067";
	z-index: 2;
	color: #FFF;
	font-size: 7px;
	width: auto;
	height: auto;
	transform: none;
}

.toggle.toggle-simple .toggle.active > label {
	background: transparent;
	color: #212529;
}

.toggle.toggle-simple .toggle.active > label:before {
	content: "\f068";
}

.toggle.toggle-minimal .toggle {
	margin: 0;
}

.toggle.toggle-minimal .toggle > label {
	background: transparent;
	color: #777;
	border-left: 0;
	border-bottom: 1px solid #CCCC;
	border-radius: 0;
	margin: 0;
	padding: 12px 20px 12px 0;
	font-weight: 600;
}

.toggle.toggle-minimal .toggle.active > label {
	background: transparent;
	border-bottom-color: #CCC;
}

