.word-rotator i, .word-rotator em, .word-rotator b {
	font-style: normal;
	font-weight: inherit;
}

.word-rotator-words {
	display: inline-flex !important;
	position: relative;
	text-align: left;
}

.word-rotator-words b {
	display: inline-block;
	position: absolute;
	white-space: nowrap;
	left: 0;
	top: 0;
}

.word-rotator-words b.is-visible {
	position: relative;
}

.word-rotator-words[class*="bg-"] {
	background: #CCC;
