/* clip */
.word-rotator.clip span {
	display: inline-block;
	padding: .2em 0;
}

.word-rotator.clip .word-rotator-words {
	overflow: hidden;
	vertical-align: top;
}

.word-rotator.clip .word-rotator-words::after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	width: 2px;
	height: 100%;
	background-color: #212529;
